@import url("https://fonts.googleapis.com/css?family=Montserrat:700");
@import url("https://use.fontawesome.com/releases/v5.0.10/css/all.css");

.profile-pg-component .setup-color-buttondiv .color-btn {
    border-radius: 50%;
    height: 80px;
    width: 80px;
    margin: 8px; }
  
  .profile-pg-component {
    padding: 10px 0; }
    .profile-pg-component .setup-profile {
      font-size: 25px;
      padding-bottom: 10px; }
    .profile-pg-component #enter-name {
      margin: 0; }
    .profile-pg-component .complete-profile-btn {
      padding: 20px; }
    .profile-pg-component .join-btn {
      font-family: "Montserrat", sans-serif;
      color: white;
      border-radius: 30px;
      font-size: 25px;
      padding: 12px 25px;
      transition: .25s; }
    .profile-pg-component .join, .profile-pg-component .join:focus, .profile-pg-component .join:active {
      background-color: #44BBA4; }
    .profile-pg-component .join:hover {
      cursor: pointer;
      background: #3a9785;
      transform: scale(0.99); }
    .profile-pg-component .cantJoin {
      background-color: #C0C0C0;
      color: grey !important; }
    .profile-pg-component .setup-color-buttondiv {
      max-width: 300px;
      margin: 0 auto;
      padding-bottom: 15px; }
      .profile-pg-component .setup-color-buttondiv .pick-a-color {
        padding: 30px 0 15px 0;
        font-size: 17px;
        overflow: hidden; }
      .profile-pg-component .setup-color-buttondiv .pick-a-color:before, .profile-pg-component .setup-color-buttondiv .pick-a-color:after {
        background-color: white;
        content: "";
        display: inline-block;
        height: 1px;
        position: relative;
        vertical-align: middle;
        width: 24%; }
      .profile-pg-component .setup-color-buttondiv .pick-a-color:before {
        right: 0.5em;
        margin-left: -50%; }
      .profile-pg-component .setup-color-buttondiv .pick-a-color:after {
        left: 0.5em;
        margin-right: -50%; }
      .profile-pg-component .setup-color-buttondiv .color-btn:hover {
        box-shadow: 0 0 2px 5px grey; }
      .profile-pg-component .setup-color-buttondiv #selected {
        box-shadow: 0 0 1px 6px grey; }
      .profile-pg-component .setup-color-buttondiv .yellow-prof {
        background-color: #FFC655; }
      .profile-pg-component .setup-color-buttondiv .blue-prof {
        background-color: #5FACFF; }
      .profile-pg-component .setup-color-buttondiv .red-prof {
        background-color: #FF6161; }
      .profile-pg-component .setup-color-buttondiv .pink-prof {
        background-color: #D45FFF; }
      .profile-pg-component .setup-color-buttondiv .green-prof {
        background-color: #44BBA4; }
      .profile-pg-component .setup-color-buttondiv .orange-prof {
        background-color: #FF8A5B; }
      .profile-pg-component .setup-color-buttondiv .purple-prof {
        background-color: #9964FF; }
      .profile-pg-component .setup-color-buttondiv .charcoal-prof {
        background-color: #444444; }
      .profile-pg-component .setup-color-buttondiv .grey-prof {
        background-color: #C0C0C0; }
  