@import url("https://fonts.googleapis.com/css?family=Montserrat:700");
@import url("https://use.fontawesome.com/releases/v5.0.10/css/all.css");

/*Giphy Search*/
.giphy-component {
    padding: 10px; 
}
.giphy-component #giphy-search-judge {
    background-color: #FF6161 !important; 
}
.giphy-component #giphy-search-instructions {
    top: 15px; 
}
.giphy-component #giphy-search-prompt {
    max-width: 450px;
    width: 100%;
    margin-top: 55px; 
}
.giphy-component .search-holder {
    width: 100%;
    max-width: 450px;
    margin: 0 auto;
}
.giphy-component .search-holder input[type="text"] {
    width: calc(100% - 65px);
    max-width: none; 
}
.giphy-component .search-holder .search-button {
    width: 50px;
    height: 50px;
    background: #FF8A5B;
    display: inline-block;
    margin: 0px 0px 0px 15px;
    position: relative;
    top: 17px;
    border-radius: 50%; 
}
.giphy-component .search-holder .search-button i {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px; 
}
.giphy-component .search-holder .search-button:hover {
    cursor: pointer;
    background: #e57c51; 
}
.giphy-component .search-holder img {
    width: 100%;
    max-height: 250px;
    margin-top: 15px;
    border-radius: 15px; 
}
.giphy-component .search-holder .button-holder {
    width: 100%;
    text-align: right;
    padding-top: 15px; 
}
  .giphy-component .search-holder .button-holder .timer-placeholder {
    display: inline-block;
    width: calc(100% - 115px);
    text-align: left;
    font-size: 30px;
    position: relative;
    bottom: 12px; 
}
.giphy-component .search-holder .button-holder .timer-placeholder i {
    padding-right: 10px; 
 }
.giphy-component .search-holder .button-holder .button {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    display: inline-block; 
}
.giphy-component .search-holder .button-holder .button:hover {
    cursor: pointer; 
}
.giphy-component .search-holder .button-holder .research {
  background: lightgray;
  position: relative; 
}
.giphy-component .search-holder .button-holder .research i {
    color: darkgrey;
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px; 
}
.giphy-component .search-holder .button-holder .research:hover {
    background: black; 
}
.giphy-component .search-holder .button-holder .confirm {
    background: #44BBA4;
    position: relative;
    margin-left: 10px; 
}
.giphy-component .search-holder .button-holder .confirm i {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px; 
}
.giphy-component .search-holder .button-holder .confirm:hover {
    cursor: pointer;
    background: #3da893; 
}
.theme-prompt {
    color: #2a7566;
}