    top: 15px;
    right: 15px;
    font-size: 20px; 
}
.giphy-component .search-holder .button-holder .research:hover {
    background: black; 
}
.giphy-component .search-holder .button-holder .confirm {
    background: #44BBA4;
    position: relative;
    margin-left: 10px; 
}
.giphy-component .search-holder .button-holder .confirm i {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px; 
}
.giphy-component .search-holder .button-holder .confirm:hover {
    cursor: pointer;
    background: #3da893; 
}
